.download-bar {
  background: #222;
  border: 5px solid #444;
  padding: 10px;
  margin: 0 -35px 20px;
  position: relative;
}

.download-bar .inner {
  overflow: hidden;
}

.download-bar .watch-fork iframe {
  display: block;
  float: left;
  border-right: 1px solid #ddd;
  padding-right: 5px;
}

.download-bar .watch-fork iframe.last {
  border-right: 0 none;
  padding-right: 0;
  padding-left: 5px;
  border-left: 1px solid #fff;
}

.download-bar .watch-fork {
  overflow: hidden;
  float: right;
  background-color: #eee;
  padding: 5px;
  border-radius: 3px;
}

.download-bar .blc {
  border: 10px solid black;
  border-color: transparent transparent black;
  width: 0;
  height: 0;
  display: block;
  position: absolute;
  bottom: -15px;
  left: 0;
  transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  /* IE9 */
  -webkit-transform: rotate(45deg);
  /* 2014 current */
}

.download-bar .trc {
  border: 10px solid black;
  border-color: black transparent transparent;
  width: 0;
  height: 0;
  display: block;
  position: absolute;
  top: -15px;
  right: 0;
  transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  /* IE9 */
  -webkit-transform: rotate(45deg);
  /* 2014 current */
}

.download-bar .avatar {
  border: 1px solid black;
  display: block;
  padding: 4px;
  float: left;
}

.download-bar .avatar img {
  display: block;
}

.download-bar a.code {
  background: transparent url(../images/code.png) no-repeat 0 2px;
  padding-left: 35px;
  margin-top: 8px;
  display: block;
  float: left;
  text-indent: 0;
  width: auto;
  height: auto;
  opacity: 1;
  filter: alpha(opacity=100);
  /* IE 5-7 */
}

.current-section {
  position: fixed;
  top: 0;
  left: 50%;
  width: 693px;
  margin-left: -352px;
  background: #222;
  border: 5px solid #444;
  color: #fff;
  opacity: 0;
  visibility: hidden;
  transition: opacity ease-in-out 0.3s;
  -webkit-transition: opacity ease-in-out 0.3s;
  /* Safari <=6.1, Android <= 4.3 */
}

.current-section p {
  padding: 5px 27px;
  font-size: 24px;
  font-weight: bold;
}

.current-section a {
  float: right;
  text-indent: -10000px;
  background: transparent url(../images/top.png) no-repeat 0 0;
  width: 20px;
  height: 20px;
  opacity: 0.8;
  margin-right: 12px;
  margin-top: 12px;
  opacity: 0.8;
  filter: alpha(opacity=80);
  /* IE 5-7 */
  transition: opacity ease-in-out 0.3s;
  -webkit-transition: opacity ease-in-out 0.3s;
  /* Safari <=6.1, Android <= 4.3 */
}

.current-section a:hover {
  opacity: 1;
  filter: alpha(opacity=100);
  /* IE 5-7 */
}

.current-section a.zip {
  margin-right: 8px;
}

a.zip,
a.zip > span {
  background: transparent url(../images/zip.png) no-repeat 0 0;
  width: 30px;
  height: 21px;
  display: inline-block;
  text-indent: -10000px;
  opacity: 0.8;
  filter: alpha(opacity=80);
  /* IE 5-7 */
  transition: opacity ease-in-out 0.3s;
  -webkit-transition: opacity ease-in-out 0.3s;
  /* Safari <=6.1, Android <= 4.3 */
}

a.tar,
a.tar > span {
  background: transparent url(../images/tar.png) no-repeat 0 0;
  width: 30px;
  height: 21px;
  display: inline-block;
  text-indent: -10000px;
  opacity: 0.8;
  filter: alpha(opacity=80);
  /* IE 5-7 */
  transition: opacity ease-in-out 0.3s;
  -webkit-transition: opacity ease-in-out 0.3s;
  /* Safari <=6.1, Android <= 4.3 */
}

a.code {
  background: transparent url(../images/code.png) no-repeat 0 2px;
  width: 30px;
  height: 21px;
  display: block;
  display: inline-block;
  text-indent: -10000px;
  opacity: 0.8;
  filter: alpha(opacity=80);
  /* IE 5-7 */
  transition: opacity ease-in-out 0.3s;
  -webkit-transition: opacity ease-in-out 0.3s;
  /* Safari <=6.1, Android <= 4.3 */
}

a.zip:hover,
a.tar:hover,
a.code:hover {
  opacity: 1;
  filter: alpha(opacity=100);
}

a.download-button {
  border: 1px solid black;
  border-radius: 3px;
  display: inline-block;
  text-indent: 0 !important;
  width: auto;
  float: right;
  background: #999;
  /* for non-css3 browsers */
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#37ADD4', endColorstr='#1B657E');
  /* IE <= 9 */
  background: -webkit-gradient(linear, left top, left bottom, from(#37ADD4), to(#1B657E));
  /* ancient webkit browsers */
  background: -webkit-linear-gradient(top, #37ADD4, #1B657E);
  /* Safari <=6.1, Android <= 4.3 */
  background: linear-gradient(to bottom, #37ADD4, #1B657E);
  height: auto;
  margin-left: 10px;
}

a.download-button > span {
  background-position: 10px 5px;
  width: auto;
  height: auto;
  padding: 5px 10px;
  padding-left: 45px;
  display: inline-block;
  text-indent: 0 !important;
  color: #fff;
}

