.nextPrev {
  margin-top: 50px;
}

.nextPrev__tag {
  color: #4d7f22;
  font-size: 0.8em;
  display: block;
}

.csstransforms .-next .nextPrev__link {
  padding-right: 50px;
}

.csstransforms .-prev .nextPrev__link {
  padding-left: 50px;
}

.nextPrev__link:hover, .nextPrev__link:focus {
  background-color: #f0f0f0;
}

.nextPrev__arrow {
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto 0;
  height: 20px;
  width: 15px;
}

.nextPrev__arrow:before {
  content: '';
  display: block;
}

.-next .nextPrev__arrow {
  right: 15px;
  top: -9px;
}

.-next .nextPrev__arrow:before {
  border: 1px solid #000;
  border-width: 0 1px 1px 0;
  transition: all 0.2s ease-in-out;
  transform-origin: right bottom;
  height: 15px;
  width: 15px;
  transform: rotate(-45deg);
  position: absolute;
  right: 1.5px;
  bottom: 4.5px;
}

.-prev .nextPrev__arrow {
  left: 15px;
}

.-prev .nextPrev__arrow:before {
  border: 1px solid #000;
  border-width: 1px 0 0 1px;
  transition: all 0.2s ease-in-out;
  transform-origin: top right;
  height: 15px;
  width: 15px;
  transform: rotate(-45deg);
  position: absolute;
  right: 3px;
}

.no-csstransforms .nextPrev__arrow {
  display: none;
}

